<template>
	<section>
		<div id="block-ingridients" :class="$route.name">
			<ul>
				<li :id="item.name" :class="$route.name" v-for="(item, i) in ingridientContent" :key="i" @click="onClick($event)">
					<a> {{ item.title }} </a>
				</li>
			</ul>
		</div>
		<animated-fade-in>
		<div id="block-ingridients-desc" :class="$route.name" v-for="(item, i) in ingridientContent" :key="i" v-if="item.name === currentIngridient">
			<div><img :src="srcIcon + item.name + '-photo.png'"></div>
			<div>
				<h3> {{ item.title }} </h3>
				<p v-html="item.content"></p>
				<app-photo-gallery :mainType="'ingridient'" :currentService="currentIngridient" :currentSubService="''"></app-photo-gallery>
			</div>
		</div>
		</animated-fade-in>
	</section>
</template>

<script>

	import appPhotoGallery from './PhotoGallery'
		
	export default {
		components: {appPhotoGallery},
		data() {
			return {
				currentIngridient: 'special_effects',
				srcIcon: 'img/' + this.$route.name + '/ingridient-',
				ingridientAdult: [
					{
						name: 'special_effects',
						title: '',
						content: '     . ,    ,   ,   .      ,      .'
					},{
						name: 'light_and_sound',
						title: '  ',
						content: '       , ,  .<br><br>          ,                .<br><br>      .'
					},{
						name: 'shooting',
						title: '  ',
						content: '       -  -.<br><br>,        .<br><br>,       .<br><br>   ,      .'
					},{
						name: 'pyrotechnics',
						title: '',
						content: '         ,      .  ,        ,  ,       .'
					},
				],
				ingridientChild: [
					{
						name: 'aquagrim',
						title: '',
						content: ' , -     ?      !          ,    (  ,  )      .      !<br><br>    ,      .'
					},{
						name: 'trampoline',
						title: '',
						content: ':   , , ,      ?       .<br><br>-,  !<br><br>     .      ,     . ,            ?<br><br>:   ,      .'
					},{
						name: 'animals',
						title: '  ',
						content: '    ,      ,     ?    ,       .      !       ,   .  ,    ,    !<br><br>       .   ,   .     .'
					},{
						name: 'special_effects',
						title: '',
						content: '     !<br><br>, .     ,      .    !<br><br>      ,      .<br><br>  (   )          .'
					},{
						name: 'light_and_sound',
						title: '  ',
						content: '          ,   ,               .<br><br>        .<br><br>        !'
					},{
						name: 'shooting',
						title: '  ',
						content: '  -  ,     !<br><br>     ,      .<br><br>     -  .       /    .       ,    ,   .'
					},{
						name: 'pyrotechnics',
						title: '',
						content: ',     ,   :    ,    ,   !    .<br><br>            .<br><br>       ,  .<br><br>     - <b>   .'
					}
				],
			}
		},
		computed: {
			ingridientContent() {
				return this.$route.name==='adult' ? this.ingridientAdult : this.ingridientChild
			}
		},
		methods: {
			onClick(e) {
				this.currentIngridient = e.currentTarget.getAttribute('id');
			}
		}
	}

</script>

<style lang="scss" scoped>

	@import '../styles/variables.scss';

	#block-ingridients {
		height: 40px;
		padding: 20px;
		text-align: center;
		&.adult {
			background-color: #F57A93;
			li, a {
				margin-right: 1%;
				color: #FDEEE9;
			}
		}
		&.child {
			background-color: $green2;
			li, a {
				margin-right: 1%;
				color: $red1;
			}
		}
	}

	#block-ingridients-desc {
		display: flex;
		justify-content: center;
		&>div {
			width: 50%;
			max-width: 600px;
		}
		&>div>img {width: 100%}
		&.adult {
			background-color: #56698F;
			h3 {
				color: #FDEEE9;
				background-color: #F57A93;
			}
		}
		&.child {
			background-color: $green1;
			h3 {
				color: #0B622F;
				background-color: $green2;
			}
		}
		a, p {color: white}
		p {font-size: 0.8em}
	}

</style>


