<style lang="scss" scoped>
	
	@import '../styles/variables.scss';

	h1 {
		font-family: arial;
		&.adult {color: $red1}
		&.child {color: white}
	}

	#block-services {
		padding: 1%;
		&.adult {background: $beige}
		&.child {background: $red1}
		ul {
			margin: 0 auto;
			width: 1200px;
		}
		li {
			display: inline-block;
			margin: 1%;
			text-align: center;
			cursor: pointer;
		}
		li.child a {color: white}
		li:hover {
			transition:  .3s;
			&.adult {background: $pink2}
			&.child {background: $blue}
		}
	}

	#block-services-desc {
		display: flex;
		justify-content: center;
		&>div {
			width: 50%;
			max-width: 600px;
		}
		&>div>img {width: 100%}
		&.adult {
			background: $pink2;
			h3 {
				color: #D93052;				
				background: $beige
			}
		}
		&.child {
			background: $blue;
			h3 {
				color: white;
				background: $red1
			}
		}
		li {
			margin-right: 20px;
			position: absolute;
			opacity: 0;
	    	transition: opacity 1s;
		}
		p {font-size: 0.8em}
	}

</style>


