<template>
	<div>
	<ul class="owl-carousel animators-carousel">
	    <li v-for="(item, i) in 73" :key="i">
	       	<img :src="'img/animators/animator-'+i+'.jpg'" @click="indexAnimator = i">
	       	<p> {{ animators[i] }} </p>
	       	<button class="btn-order"></button>
	    </li>
	</ul>
	<gallery :images="images" :index="indexAnimator" @close="indexAnimator = null"></gallery>
	</div>
</template>

<script>

	import VueGallery from 'vue-gallery';

	export default {
		components: {'gallery': VueGallery},
		data() {
			return {
				animators: ['   ',' ','    ',' ,   ','','   ','   ','',' ',' ',' ',' ',', , ',' ','  ','','','',' ',' ',' ',' ','',' ','','',' ',' ',' ',' -',' ',' ',' ','   ','   ',' ','  1','  2','','   ',' ',',  ',' Winx ','',' Winx ','    ','  ',' ','      ', ' .  ','',' ','   ','',' ','','','  ','  ','  ','  ','  ','   ','',' ',' ','   ','','','','  ','','   '],
				images: [],
				indexAnimator: null
			}
		},
		methods: {
			installOwlCarousel() { 
			    $('.animators-carousel').owlCarousel({ 
					loop:true,
					mouseDrag:true,
					touchDrag:true,
					autoplay:true,
					autoplayTimeout:2000,
					autoplayHoverPause:true,
					items:8,
					margin:20
				})
			}
		},
		mounted() {
			installGallery: {
				for (let i = 0; i < 73; i++) {
					this.images.push('img/gallery/animators/animator-'+i+'.jpg')
				}
			}
			owlCarousel: {
				this.installOwlCarousel()
			}
		}
	}

</script>

<style lang="scss">

	@import '../styles/variables.scss';

	#block-animators .owl-carousel {
		width: $main_width;
		margin: 0 auto;
		li {width: 150px}
		img {cursor: pointer}
		p {height: 40px}
		button {
			width: 100px;
			height: 25px;
			font-weight: 700;
			font-size: 0.8em;
		}
	}

</style>


